import { Inject, Injectable, InjectionToken, Optional } from '@angular/core';

export interface TurnoversJourneyConfiguration {
  /**
   * Wether to show the forecasted data or not
   */
  showForecast: boolean;
}

// eslint-disable-next-line
export const TurnoversJourneyConfigurationToken = new InjectionToken<TurnoversJourneyConfiguration>(
  'TurnoversJourneyConfiguration injection token',
);

const configDefaults: TurnoversJourneyConfiguration = {
  showForecast: true,
};

@Injectable()
export class TurnoversJourneyService {
  private _config: TurnoversJourneyConfiguration;

  constructor(
    @Optional()
    @Inject(TurnoversJourneyConfigurationToken)
    config: TurnoversJourneyConfiguration,
  ) {
    config = config || {};
    this._config = { ...configDefaults, ...config };
  }

  get defaults(): TurnoversJourneyConfiguration {
    return configDefaults;
  }

  get showForecast(): boolean {
    return this._config.showForecast;
  }
}
